//-------    HEADER    -------\\
header{
  position: relative;
  width: 100%;
  height: 100px;
  background-color: #f7f9f0;
  z-index: 2;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;

  .topbar-burger{
    padding: 40px;
    min-width: 25px;
    min-height: 20px;
  }

  .topbar-logo{
    text-align: center;
    width: 170px;
    height: 80px;
    padding: 10px;
  }

  .btn-reserve{
    background-color: #93a446;
    margin: 25px 40px;
    min-width: 130px;
    height: 50px;
    display: flex;
    color: #ffffff;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    text-decoration: none;
  }
}

//-------    CONTENT    -------\\
//-------         Grid-Section
@mixin center(){
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.content{
  margin: 0 15% ;
  //------         Grid-element
  section{
    margin: 20% 0;
    height: 100%;
    //display: grid;
    //grid-template-columns: repeat(5, 20%);
    .text_article{
      @include center;
    }
  }

  //-------         PARALLAX

  .parallax {

    position: relative;
    //grid-template-rows: 1fr 1fr;

  }

  .layer {
    height: auto;
    position: absolute;
    z-index: -1;
  }

  .layer-1{
    right: 40%;
    width: 70%;
  }

  .layer-2{
    width: 50%;
    left: 50%;
    top: 50%;
  }





  .article2{
    display: grid;
    grid-template-rows: 460px 420px 1fr;
    .image1{
      grid-row: 2/4;
      grid-column: 1;
      width: 640px;
      height: auto;
      z-index: 2;
    }

    .text{
      grid-column: 1 / span 2;
      grid-row: 1 ;
    }

    .image2{
      grid-row: 1 / 4;
      grid-column: 3;
      width: 640px;
      height: auto;
    }

  }

}


